package view

import androidx.compose.runtime.Composable
import dict.*
import lib.*
import mvi.Intent
import org.jetbrains.compose.web.css.*
import org.jetbrains.compose.web.dom.Br
import org.jetbrains.compose.web.dom.Button
import org.jetbrains.compose.web.dom.Div
import org.jetbrains.compose.web.dom.Text
import kotlin.js.Date

val YES_NO_PT = 90
val WORD_PT = 44

@Composable
fun RenderAppState(state: State, sendIntent: (Intent) -> Unit) {
    when (state.screen) {
        is Screen.Dictionaries -> {
            TxtButton(" ") {
                BrowserStorage.clear()
            }
            Txt(" : " + state.deployTime, 24)
            allDictionaries.forEach { dictionary: Dictionary ->
                Div {
                    val selected = state.screen.selected.contains(dictionary)
                    CheckBox(
                        " ${dictionary.name} (${dictionary.words.size} )",
                        selected
                    ) {
                        sendIntent(Intent.ChooseDictionary(dictionary))
                    }
                }
            }
            TxtButton("", YES_NO_PT) {
                sendIntent(Intent.StartWordScreen)
            }
            Txt("BUILD_TIME at ${conf.BuildConfig.BUILD_TIME}", 20)
        }
        is Screen.Words -> {
            when (state.screen.wordState) {
                is WordState.Hidden -> {
                    Text("  ?")
                    Br { }
                    Br { }
                    TxtButton(" ") {
                        sendIntent(Intent.OpenWord)
                    }
                    Br { }
                    Br { }
                    Txt(state.screen.word.hint, WORD_PT, "bold")
                    Br {}
                    GreenBtn("") {
                        sendIntent(Intent.MarkWord(true))
                    }
                    Text("...")
                    RedBtn("") {
                        sendIntent(Intent.MarkWord(false))
                    }
                }
                is WordState.Open -> {
                    Text("  ?")
                    Br {}
                    Br {}
                    Txt(state.screen.word.hint, WORD_PT)
                    Br {}
                    Br {}
                    Div({
                        style {
                            fontWeight("bold")
                            fontSize(WORD_PT.pt)
                        }
                    }) {
                        Text(state.screen.word.secret)
                    }
                    Br {}
                    GreenBtn("") {
                        sendIntent(Intent.MarkWord(true))
                    }
                    Text("...")
                    RedBtn("") {
                        sendIntent(Intent.MarkWord(false))
                    }
                }
                is WordState.Fail -> {
                    Text("  :")
                    Br {}
                    Br {}
                    Txt(state.screen.word.hint, WORD_PT)
                    Br {}
                    Br {}
                    Txt(state.screen.word.secret, WORD_PT, "bold")
                    Br {}
                    TxtButton("", YES_NO_PT) {
                        sendIntent(Intent.NextWord)
                    }
                }
            }
            Br {}
            Br {}
            Div({
                style {
                    fontSize(20.pt)
                    color(Color("gray"))
                }
            }) {
                val stat = BrowserStorage.getItem(state.screen.word.hint) ?: StoreItem()
                Text("   :")
                Br {}
                Br {}
                Text(": ${stat.successCount}, : ${stat.failCount},")
                Br {}
                Br {}
                Text(
                    ": " + if (stat.changedUnixTime == 0) " " else Date(stat.changedUnixTime * 1000L).toLocaleString()
                )
            }
        }
    }

}

@Composable
fun TxtButton(label: String, size: Int = 30, styleBuilder: StyleBuilder.() -> Unit = {}, action: () -> Unit) {
    Button(
        attrs = {
            this.onClick {
                action()
            }
        }
    ) {
        Div({
            style {
                fontSize(size.pt)
                styleBuilder()
            }
        }) {
            Text(label)
        }
    }
}

@Composable
fun GreenBtn(label: String, action: () -> Unit) {
    TxtButton(label, size = YES_NO_PT, {
        color(Color("#006400"))
    }, action)
}

@Composable
fun RedBtn(label: String, action: () -> Unit) {
    TxtButton(label, size = YES_NO_PT, {
        color(Color("#8B0000"))
    }, action)
}

@Composable
fun Txt(label: String, size: Int, weight:String = "normal") {
    Div({
        style {
            fontSize(size.pt)
            fontWeight(weight)
        }
    }) {
        Text(label)
    }
}
